:root {
    --color-gray-light: #6d6d6d;

    --color-gray-dark-2: #353535;
    --color-gray-dark-3: #1e1e1e;

    --color-white: #fff;
    --color-blue: #2196F3;
    --color-red: #fb3838;
    --color-red-light: #f74c4c;

    --color-book-1: #ffdf8f;
    --color-book-2: #8cbae0;
    --color-book-3: #dc4954;
}

*,
*::after,
*::before {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
html {
    font-size: 62.5%;
}
body {
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 400;
    color: #fff;
}

.container {
    display: grid;
    grid-template-columns: [full-start] 5vw [center-start] repeat(6, 1fr) [center-end] 5vw [full-end];
    grid-template-rows: 100vh 1fr min-content;
}


